// TODO: Fix nested components
/* eslint-disable react/no-unstable-nested-components */
import { MDCTextField, MDCTextFieldHelperText } from '@material/textfield';
import clsx from 'clsx';
import { addProps } from '@element/react-common';
import React, {
    forwardRef,
    useCallback,
    useEffect,
    useImperativeHandle,
    useMemo,
    useRef,
    useState
} from 'react';
import { FloatingLabel } from '@element/react-floating-label';
import { LineRipple } from '@element/react-line-ripple';
import { Icon } from '@element/react-icon';
import { NotchedOutline } from '@element/react-notched-outline';
import { TooltipText } from '@element/react-tooltip';
import { edsRandomId } from '@element/shared';
import { createPopper } from '@popperjs/core';
import TextfieldSpanShim from './TextfieldSpanShim';
import {
    TextfieldDefaultProps,
    TextfieldPropTypes
} from './generated/propTypes';

const Counter = (hasCounter, currentMaxlength) => {
    if (hasCounter) {
        return (
            <div className='mdc-text-field-character-counter'>
                0 / {currentMaxlength}
            </div>
        );
    }
    return null;
};

const LeadingIcon = leadingIcon => {
    const { type } = { ...leadingIcon };
    const { displayName } = { ...type };

    if (typeof leadingIcon === 'string') {
        return (
            <Icon
                role='button'
                className='mdc-text-field__icon lmnt-text-field__icon mdc-text-field__icon--leading'
                icon={leadingIcon}
            />
        );
    }

    return addProps(leadingIcon, {
        className: clsx(
            'mdc-text-field__icon lmnt-text-field__icon mdc-text-field__icon--leading',
            displayName === 'IconButton' &&
                'lmnt-text-field__icon-button--leading'
        ),
        dense: displayName === 'IconButton' ? true : undefined,
        role: 'button'
    });
};

const TrailingIcon = trailingIcon => {
    const { type } = { ...trailingIcon };
    const { displayName } = { ...type };
    if (typeof trailingIcon === 'string') {
        return (
            <Icon
                className='mdc-text-field__icon lmnt-text-field__icon mdc-text-field__icon--trailing'
                icon={trailingIcon}
            />
        );
    }
    return addProps(trailingIcon, {
        className: clsx(
            'mdc-text-field__icon lmnt-text-field__icon mdc-text-field__icon--trailing',
            displayName === 'IconButton' &&
                'lmnt-text-field__icon-button--trailing'
        ),
        dense: displayName === 'IconButton' ? true : undefined,
        role: displayName === 'IconButton' ? 'button' : null
    });
};

const Textfield = forwardRef((props, externalRef) => {
    const {
        block,
        className,
        containerClass,
        containerProps,
        containerStyle,
        counter,
        defaultValue,
        dense,
        disabled,
        embedded,
        filled,
        focused,
        fullWidth,
        helperText,
        helperTextId,
        helperTextPersistent,
        helperTextValidation,
        id,
        label,
        labelId,
        leadingContent,
        leadingIcon,
        maxLength,
        maxlength,
        noResize,
        onBlur,
        outlined,
        placeholder,
        prefixText,
        primary = null,
        search,
        secondary,
        suffixText,
        textarea,
        tooltip,
        trailingContent,
        trailingIcon,
        type,
        valid,
        value,
        variant,
        ...customProps
    } = props;

    const { hoisted, position = 'bottom' } = tooltip && typeof tooltip === 'object' ? tooltip : {};
    // TODO: some of the logic below will get simpler when v6 removes
    // deprecated properties
    const [idActual] = useState(() => id || edsRandomId());
    const [helperTextIdActual] = useState(() => helperTextId || edsRandomId());
    const [labelIdActual] = useState(() => labelId || edsRandomId());
    const [isBlockElement, setIsBlockElement] = useState(true);
    const [hasCounter, setHasCounter] = useState(false);
    const [currentMaxlength, setCurrentMaxlength] = useState(maxlength);
    const hasHelperLine = helperText || (counter && currentMaxlength);
    const Tag = textarea ? 'textarea' : 'input';
    const ref = useRef();
    const helperTextDomRef = useRef();
    const helperTextRef = useRef();
    const mdcTextFieldRef = useRef();
    const labelRef = useRef();
    const inputRefActual = useRef();
    const containerRef = useRef();
    const tooltipRef = useRef();
    const popperRef = useRef();
    const hasIcon = (trailingIcon || leadingIcon) && !textarea;
    const isValid = useMemo(
        () => (valid === undefined || valid === null ? true : valid),
        [valid]
    );
    const isEmbedded =
        !textarea && (embedded || search || variant === 'embedded');
    const isOutlined =
        !embedded && (outlined || secondary || variant === 'outlined');
    const isFilled =
        !isOutlined &&
        !isEmbedded &&
        (primary || filled || variant === 'filled');
    const isFullWidth = isEmbedded || fullWidth || block;
    const hasLabel = !isEmbedded && !!label && !dense;

    useEffect(() => {
        mdcTextFieldRef.current = MDCTextField.attachTo(labelRef.current);
        return () => {
            mdcTextFieldRef.current.destroy();
        };
    }, [hasCounter]);

    useImperativeHandle(externalRef, () => ({
        mdc: mdcTextFieldRef.current,
        input: inputRefActual.current,
        container: containerRef.current,
        label: labelRef.current
    }));

    // TODO: in testing, I removed this code and I could not reproduce the issue this originally solved - do we still need it?
    useEffect(() => {
        // HACK: this breaks when helperText is markup instead of string, so now we bypass it in that case
        if (helperText && typeof helperText === 'string') {
            helperTextRef.current = new MDCTextFieldHelperText(
                helperTextDomRef.current
            );
        }

        return () => {
            helperTextRef.current && helperTextRef.current.destroy();
        };
    }, [helperText]);

    useEffect(() => {
        if (helperText) {
            helperTextRef.current?.foundation.setValidation(
                helperTextValidation
            );
            helperTextRef.current?.foundation.setValidity(isValid);
            helperTextRef.current?.foundation.setPersistent(
                helperTextPersistent
            );
            helperTextRef.current?.foundation.setContent(helperText);
        }
    }, [helperText, helperTextPersistent, helperTextValidation, isValid]);

    useEffect(() => {
        // NOTE: These value and valid need to be set together otherwise mcd will "forget" the valid state.
        if (!defaultValue)
            mdcTextFieldRef.current.value = value || value === 0 ? value : '';
        // NOTE: valid prop is undefined until user decides valid is true or false
        // with custom validation.
        mdcTextFieldRef.current.valid = isValid;
    }, [mdcTextFieldRef, value, isValid, defaultValue]);

    useEffect(() => {
        mdcTextFieldRef.current.disabled = disabled;
    }, [mdcTextFieldRef, disabled]);

    // TODO: This goes away in v6
    useEffect(() => {
        setCurrentMaxlength(maxLength && !maxlength ? maxLength : maxlength);
    }, [maxlength, maxLength]);

    useEffect(() => {
        setHasCounter(currentMaxlength > 0 && counter);
    }, [currentMaxlength, counter]);

    useEffect(() => {
        const isBlock = isFullWidth || counter || helperText;
        setIsBlockElement(isBlock);
    }, [isFullWidth, counter, helperText]);

    // NOTE: Ignoring testing on methods below. Testing library doesn't
    // recognize events. These methods do work when called outside of tests.
    /* istanbul ignore next */
    const handleValidState = () => {
        if (valid !== undefined && value !== null) {
            mdcTextFieldRef.current.valid = valid;
        }
    };

    /* istanbul ignore next */
    const handleBlur = event => {
        if (onBlur) onBlur(event);
        handleValidState();
    };

    const handleFormReset = () => {
        mdcTextFieldRef.current.value = '';
        mdcTextFieldRef.current.valid = isValid;
    };

    useEffect(() => {
        /* eslint-disable no-undef */
        window.addEventListener('reset', handleFormReset);

        return () => {
            window.removeEventListener('reset', handleFormReset);
        };
        /* eslint-enable no-undef */
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    useEffect(() => {
        if (tooltip) {
            const modifiers = [
                {
                    name: 'flip',
                    options: {
                        enabled: true,
                        padding: { bottom: 32, top: 32 }
                    }
                },
                {
                    name: 'offset',
                    options: {
                        offset: [0, 12]
                    }
                }
            ];

            popperRef.current = createPopper(ref.current, tooltipRef.current, {
                placement: position,
                modifiers
            });
        }
        return () => {
            popperRef.current?.destroy();
        };
    }, [position, hoisted, tooltip]);

    const showTooltip = useCallback(() => {
        if (value) {
            // eslint-disable-next-line no-underscore-dangle
            const input = mdcTextFieldRef.current.input_;
            if (input.offsetWidth < input.scrollWidth) {
                tooltipRef.current?.setAttribute('data-show', '');
                popperRef.current?.setOptions(options => ({
                    ...options,
                    modifiers: [
                        ...options.modifiers,
                        { name: 'eventListeners', enabled: true }
                    ]
                }));

                popperRef.current?.update();
            }
        }
    }, [value]);

    const hideTooltip = useCallback(() => {
        tooltipRef.current?.removeAttribute('data-show');
        popperRef.current?.setOptions(options => ({
            ...options,
            modifiers: [
                ...options.modifiers,
                { name: 'eventListeners', enabled: false }
            ]
        }));
    }, []);

    return (
        <div
            ref={containerRef}
            style={containerStyle}
            className={clsx(
                'lmnt',
                'lmnt-text-field--container',
                isFullWidth && 'lmnt-text-field--container-full-width',
                !isFullWidth &&
                    isBlockElement &&
                    'lmnt-text-field--container-to-content-width',
                !isBlockElement && 'lmnt-text-field-inline-container',
                containerClass
            )}
            {...containerProps}
            onMouseEnter={tooltip && showTooltip}
            onFocus={tooltip && showTooltip}
            onMouseLeave={tooltip && hideTooltip}
            onBlur={tooltip && hideTooltip}
        >
            {leadingContent}
            <label
                htmlFor={idActual}
                ref={labelRef}
                className={clsx([
                    'lmnt',
                    'mdc-text-field',
                    'lmnt-text-field',
                    isEmbedded && 'lmnt-text-field--embedded',
                    isFullWidth && 'lmnt-text-field--full-width',
                    hasIcon && 'lmnt-text-field--with-icon',
                    !isValid && 'mdc-text-field--invalid',
                    disabled && 'mdc-text-field--disabled',
                    focused && 'mdc-text-field--focused',
                    !hasLabel && 'mdc-text-field--no-label',
                    leadingIcon &&
                        !textarea &&
                        'mdc-text-field--with-leading-icon',
                    textarea && 'mdc-text-field--textarea',
                    textarea &&
                        counter &&
                        currentMaxlength &&
                        !noResize &&
                        'mdc-text-field--with-internal-counter',
                    trailingIcon &&
                        !textarea &&
                        'mdc-text-field--with-trailing-icon',
                    isFilled && 'mdc-text-field--filled',
                    isOutlined && 'mdc-text-field--outlined',
                    dense && 'lmnt-text-field--dense',
                    className
                ])}
            >
                {isFilled && <span className='mdc-text-field__ripple' />}

                {/* NOTE: leadingIcon cannot be used with textarea and not at the same time as prefixText */}
                {leadingIcon && !textarea && LeadingIcon(leadingIcon)}

                {/* NOTE: prefixText is shown on focus and cannot be used with leadingIcon or textarea */}
                {prefixText && !leadingIcon && !textarea && (
                    <span className='mdc-text-field__affix mdc-text-field__affix--prefix'>
                        {prefixText}
                    </span>
                )}

                {/* NOTE: SpanShim is used to render span with resizer class when wrap equals !noResize and textarea */}
                {/* If noResize, then the tag is rendered on its own without additional span and class. */}
                {/* Counter options for textarea are within the resizer span, */}
                {/* or inside the helper line when noResize and textarea */}
                <TextfieldSpanShim wrap={!noResize && textarea}>
                    <Tag
                        aria-controls={helperTextIdActual}
                        aria-describedby={helperTextIdActual}
                        aria-labelledby={labelIdActual}
                        className={clsx(
                            'mdc-text-field__input',
                            type === 'color' && 'lmnt-text-field__input--color'
                        )}
                        disabled={disabled}
                        id={idActual}
                        type={type}
                        onBlur={handleBlur}
                        defaultValue={defaultValue}
                        placeholder={
                            placeholder ||
                            (isEmbedded && label ? label : '') ||
                            (dense && label ? label : '')
                        }
                        maxLength={currentMaxlength}
                        ref={inputRefActual}
                        value={value}
                        {...customProps}
                    />
                    {!noResize && Counter(hasCounter, currentMaxlength)}
                </TextfieldSpanShim>

                {/* NOTE: trailingIcon cannot be used with textarea and not at the same time as suffixText */}
                {trailingIcon && !textarea && TrailingIcon(trailingIcon)}

                {/* NOTE: suffixText is shown on focus and cannot be used with trailingIcon or textarea */}
                {suffixText && !trailingIcon && !textarea && (
                    <span className='mdc-text-field__affix mdc-text-field__affix--suffix'>
                        {suffixText}
                    </span>
                )}

                {/* NOTE: hasOutline renders an outline around textfield when variant='outline' and a label if one is provided */}
                {isOutlined && (
                    <NotchedOutline>
                        {hasLabel && (
                            <FloatingLabel
                                id={labelIdActual}
                                htmlFor={idActual}
                            >
                                {label}
                            </FloatingLabel>
                        )}
                    </NotchedOutline>
                )}

                {/* NOTE: textfield variant='filled' renders floating label if label is provided */}
                {isFilled && hasLabel && (
                    <FloatingLabel id={labelIdActual} htmlFor={idActual}>
                        {label}
                    </FloatingLabel>
                )}

                {(isFilled || isEmbedded) && <LineRipple />}
            </label>
            {trailingContent}
            {hasHelperLine && (
                <div className='mdc-text-field-helper-line'>
                    {helperText && (
                        <div
                            ref={helperTextDomRef}
                            id={helperTextIdActual}
                            aria-hidden={
                                helperTextPersistent ? 'false' : 'true'
                            }
                            className={clsx([
                                'mdc-text-field-helper-text',
                                helperTextPersistent &&
                                    'mdc-text-field-helper-text--persistent',
                                helperTextValidation &&
                                    'mdc-text-field-helper-text--validation-msg'
                            ])}
                        >
                            {helperText}
                        </div>
                    )}
                    {noResize && Counter(hasCounter, currentMaxlength)}
                </div>
            )}
            {tooltip && (
                <TooltipText
                    role='tooltip'
                    {...tooltip}
                    ref={tooltipRef}
                    text={value}
                />
            )}
        </div>
    );
});

Textfield.displayName = 'Textfield';
Textfield.propTypes = TextfieldPropTypes;
Textfield.defaultProps = TextfieldDefaultProps;

export default Textfield;
